// @component
// @@header
// sites/all/modules/custom/vactory_header/scss/_variant1.scss
// Variant 1
// -----------------------------------------------------------------------------

/* === VARIABLES === */
$vh1-primary-color: $primary !default;
$vh1-primary-links-color: #999 !default;
$vh1-primary-links-active-color: $primary !default;
$vh1-secondary-links-color: #fff !default;
$vh1-heading-color: #afdefa !default;

/* === COMPONENTS === */

// Desktop > Layout.
// -----------------------------------------------------------------------------
.vh-variant1 .vh-layout {
  display: flex;
  align-items: center;
}

// Desktop > Brand.
// -----------------------------------------------------------------------------
.vh-variant1 .navbar-brand {
  float: none;
  padding: 10px 0;
  margin-bottom: 5px;
  display: block;

  > img {
    max-width: 150px;
    min-width: 150px;
  }
}

@include media-breakpoint-down(md) {
  .vh-header--branding {
    flex: 1;
  }
}

// Desktop > Primary Menu.
// -----------------------------------------------------------------------------
.vh-variant1 {

  .cbp-hrmenu {
    flex-grow: 1;
  }

  .cbp-hrmenu a {
    font-family: $stylishfont;
  }

  /* general ul style */
  .cbp-hrmenu ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  /* first level ul style */
  .cbp-hrmenu > .menu-level--1 {
    width: 90%;
    max-width: 70em;
    margin: 0 auto;
    padding: 0 15px;
  }

  .cbp-hrmenu .menu-sub-inner--1 {
    @include make-container();
    @include make-container-max-widths();
  }

  .cbp-hrmenu > ul > li {
    display: inline-block;
  }

  .cbp-hrmenu > ul > li > a {
    font-weight: 700;
    padding: 2em 1em;
    color: $vh1-primary-links-color;
    display: inline-block;
  }

  .cbp-hrmenu > ul > li > a:hover {
    color: $vh1-primary-color;
    text-decoration: none;
  }

  .cbp-hrmenu > ul > li.cbp-hropen a,
  .cbp-hrmenu > ul > li.cbp-hropen > a:hover {
    color: $vh1-secondary-links-color;
    background: $vh1-primary-color;
  }

  /* sub-menu */
  .cbp-hrmenu .menu-sub--1 {
    display: none;
    position: absolute;
    background: $vh1-primary-color;
    width: 100%;
    left: 0;
    z-index: 3;
  }

  .cbp-hropen .menu-sub--1 {
    display: block;
    padding-bottom: 3em;
  }

  .cbp-hrmenu .menu-level--2 > li {
    width: 33%;
    float: left;
    padding: 0 2em 0;
  }

  .cbp-hrmenu .menu-sub-inner--1:before,
  .cbp-hrmenu .menu-sub-inner--1:after {
    content: " ";
    display: table;
  }

  .cbp-hrmenu .menu-sub-inner--1:after {
    clear: both;
  }

  .cbp-hrmenu .menu-level--2 > li a {
    line-height: 2em;
  }

  .cbp-hrmenu .menu-level--2 > li > a {
    display: block;
    color: $vh1-heading-color;
    padding: 2em 0 0.6em;
    margin: 0;
    font-size: 160%;
    font-weight: 300;
  }
}

