// Hamburger.
// -----------------------------------------------------------------------------
.vh-variant1 {
  .vh-hamburger {
    button:focus {
      outline: 0;
    }
  }

  .hamburger {
    padding: 0;
  }

  .hamburger-box {
    width: 30px;
  }

  .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    width: 30px;
    height: 2px;
  }
}

// Mobile > Off canvas layer.
// -----------------------------------------------------------------------------
.vhm-variant1 {
  &.c-offcanvas {
    padding: 10px 15px;
  }

  &.c-offcanvas--left,
  &.c-offcanvas--right {
    width: 17em;
  }

  &.c-offcanvas--left {
    transform: translate3d(-17em, 0, 0);
  }

  &.c-offcanvas--right {
    transform: translate3d(17em, 0, 0);
  }

  &.is-open {
    transform: translate3d(0, 0, 0);
  }
}

// Mobile > Brand.
// -----------------------------------------------------------------------------
.vhm-variant1 .navbar-brand {
  float: none;
  padding: 0;
  height: auto;
  display: block;

  img {
    max-width: 150px;
    min-width: 150px;
  }
}

// Mobile > Search block.
// -----------------------------------------------------------------------------
.vhm-variant1 .vhm__search-form {
  .navbar-form {
    padding: 0;
    margin: 15px 0 0 0;

    .form-item-keys {
      flex-grow: 1;
    }

    .form-control {
      width: 100%;
    }
  }

  .block--search form {
    display: flex;
  }

  //input[type="submit"] {
  //  /*!*/
  //  @extend %btn-secondary;
  //}
}

// Mobile > Primary menu.
// -----------------------------------------------------------------------------
.vhm-variant1 .vhm__menu {
  ul {
    padding: 0 0 0 17px;
  }

  > ul {
    padding: 0;
    list-style: none;
  }

  a {
    padding: 10px 0;
    display: block;
    position: relative;

    &:hover {
      text-decoration: none;
    }

    &.is-active-trail,
    &.is-active {
      font-weight: bold;
    }

    &.is-active-trail {
      color: $vh1-primary-links-active-color;
    }
  }

  .has-sub > div {
    display: none;
  }

  .has-sub > a {
    &::after {
      @extend %icon; // scss-lint:disable ExtendDirective
      content: $icon-chevron-right-flat;
      display: inline-block;
      font-size: 15px;
      position: absolute;
      top: 50%;
      right: 13px;
      transform: translateY(-50%) rotate(0);
    }

    // Transform arrow to cross.
    &.selected {
      &::after {
        transform: translateY(-50%) rotate(90deg);
      }
    }
  }
}
