// @page
// themes/interface/src/scss/stylesheets/pages/_user.scss
// User
// -----------------------------------------------------------------------------

$link-facebook-colors: (
        background: #3B5998,
        background_alt: #4568b2,
        border: #3B5998,
        border_alt: #3B5998,
        text: #FFF,
        text_alt: #FFF,
        icon: #FFF,
) !default;

$link-google-colors: (
        background: #fff,
        background_alt: #fff,
        border: #c4c4c4,
        border_alt: #aaa,
        text: #484848,
        text_alt: #484848,
        icon: #dd4e42,
) !default;

// Form wrapper.
.vf-user-login-form {
  max-width: 450px;
  margin: 0 auto;
  border: 1px solid $extra-light;
  padding: 32px;
}

// Form Buttons.
.vf-user-login-form .form-submit {
  @include factory-button-size-lg();

  // .btn-block
  display: block;
  width: 100%;
}

// Form Seperator.
// ==============
.form-or-separator {
  background: none;
  height: 34px;
  position: relative;
  text-align: center;
}

.form-or-separator--text {
  background-color: $white;
  display: inline-block;
  padding: 8px;
  font-size: 14px;
  font-weight: bold;
  color: $gray-700;
  margin: 0;
}

.form-or-separator hr {
  border: 0;
  border-top: 1px solid $gray-400;
  margin: -16px auto 10px;
  width: 100%;
}

// Form Oauth.
.block--socialauthlogin .auth-link {
  @include factory-button();
  @include factory-button-size-lg();

  display: block !important;
  border: 1px solid !important;
  width: 100% !important;
  height: auto !important;
  background-image: none !important;
  margin: 0 !important;
  margin-bottom: 6px !important;
  text-rendering: optimizelegibility;
  font-size: 16px !important;
}

.auth-link {
  position: relative;

  &::after {
    @extend %icon;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
    font-size: 16px;
    width: 21px;
    text-align: center;
    pointer-events: none;
    user-select: none;
  }

  &[href*="facebook"]::after {
    content: $icon-facebook;
    color: map-get($link-facebook-colors, icon);
  }

  &[href*="google"]::after {
    content: $icon-googleplus;
    color: map-get($link-google-colors, icon) !important;
  }
}

.auth-link[href*="facebook"] {
  background-color: map-get($link-facebook-colors, background) !important;
  border-color: map-get($link-facebook-colors, border) !important;
  color: map-get($link-facebook-colors, text) !important;

  &:hover,
  &:focus {
    background-color: map-get($link-facebook-colors, background_alt) !important;
    border-color: map-get($link-facebook-colors, border_alt) !important;
    color: map-get($link-facebook-colors, text_alt) !important;
  }
}

.auth-link[href*="google"] {
  background: map-get($link-google-colors, background) !important;
  border-color:  map-get($link-google-colors, border) !important;
  color: map-get($link-google-colors, text) !important;

  &:hover,
  &:focus {
    background: map-get($link-google-colors, background_alt) !important;
    border-color:  map-get($link-google-colors, border_alt) !important;
    color: map-get($link-google-colors, text_alt) !important;
  }
}
