// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Typography.
// -----------------------------------------------------------------------------
$body-font: 'Lato', sans-serif;
$headingfont: 'Montserrat', sans-serif;
$stylishfont: 'PT Sans', sans-serif;

// Theme colors.
// -----------------------------------------------------------------------------
$body-color: #ffffff;
$body-text-weight: 400;
$headingtext: #3e4555;
$body-text: #3c3c3c;
$themecolor: #2196f3;
$themecolor-alt: #26c6da;
$themecolor-dark: #028ee1;

// Bootstrap colors.
// -----------------------------------------------------------------------------
$danger: #ff4d7e;
$success: #2cdd9b;
$warning: #fec500;
$primary: $themecolor;
$info: #316ce8;
$inverse: #3e4555;
$muted: #8d97ad;
$dark: #263238;
$light: #f4f8fa;
$extra-light: #ebf3f5;
$secondary: #727b84;

// Social.
// -----------------------------------------------------------------------------
$facebook: #3b5a9a;
$twitter: #56adf2;
$google: #dd4e42;

// Light colors.
// -----------------------------------------------------------------------------
$light-danger: #f9e7eb;
$light-success: #e8fdeb;
$light-warning: #fff8ec;
$light-primary: #f1effd;
$light-info: #cfecfe;
$light-inverse: #f6f6f6;
$light-megna: #e0f2f4;

// Dark colors.
// -----------------------------------------------------------------------------
$danger-dark: #d73e6b;
$success-dark: #1dc8cc;
$warning-dark: #dcaf17;
$primary-dark: #345bcb;
$info-dark: #2c73cb;
$red-dark: #d61f1f;
$inverse-dark: #232a37;
$dark-transparent: rgba(0, 0, 0, 0.05);

// Normal colors.
// -----------------------------------------------------------------------------
$white: #ffffff;
$red: #fb3a3a;
$yellow: #fed700;
$purple: #7460ee;
$blue: #02bec9;
$megna: #1dc8cd;
$orange: #ff6a5b;

// Extra variables.
// -----------------------------------------------------------------------------
$rgt: right;
$lft: left;
$border: rgba(120, 130, 140, 0.13);
$radius: 4px;
$card-shadow: 0px 0px 30px rgba(115, 128, 157, 0.1);
$img-shadow: 0px 0px 30px rgba(115, 128, 157, 0.3);

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret:              true !default;
$enable-rounded:            false !default;
$enable-shadows:            false !default;
$enable-gradients:          false !default;
$enable-transitions:        true !default;
$enable-hover-media-query:  false !default;
$enable-grid-classes:       true !default;
$enable-print-styles:       true !default;

// Components
//
// Define common padding and border radius sizes and more.
$border-radius: 0;
$border-radius-lg: 0;
$border-radius-sm: 0;

// Icomoon path.
// -----------------------------------------------------------------------------
$icomoon-font-path: "../fonts/icons-font/vactory-icons-font/fonts" !default;

// Bagdes.
// -----------------------------------------------------------------------------
$badge-font-size: 70% !default;

// Breadcrumb.
// -----------------------------------------------------------------------------
$breadcrumb-divider-color: $body-text;

// Colors.
$primary-color: $primary;
$white: #FFFFFF;
$black: #000000;
$font-color: #444444;

// Sizes.
$font-size: 16px;
$line-height: 1.5;
$heading-margin: 0.7em;

// Block
$block-padding: 100px;

// Buttons.
$btn-padding: 9px 26px;
$btn-bg-color: $primary-color;
$btn-text-color: #FFF;
$btn-hover-text-color: #FFF;
$btn-hover-bg-color: darken($btn-bg-color, 10%);

$btn-white-bg-color: $white;
$btn-white-text-color: $black;
$btn-white-hover-text-color: $btn-white-text-color;
$btn-white-hover-bg-color: #DEDEDE;

$btn-black-bg-color: $black;
$btn-black-text-color: $white;
$btn-black-hover-text-color: #FFF;
$btn-black-hover-bg-color: lighten($btn-black-bg-color, 5%);

//$btn-border-radius: 10px;

$btn-shadow: 1;

$headings: (
        h1: 2.75rem,
        h2: 2rem,
        h3: 1.125rem,
        h4: 1rem,
        h5: 0.9rem,
        h6: 0.75rem
) !default;
