.card { }

@include media-breakpoint-up(md) {
  .card--inline {
    flex-direction: row;
  }

  .card--inline .card-image--inline {
    flex-basis: 41%;
  }

  .card--inline .card-body--inline {
    flex-basis: 59%;
  }
}

.card-body {
  display: flex;
  flex-direction: column;
}

.card-read-more {
  margin-top: auto;
}


.date-pos {
  padding: 10px;
  display: inline-block;
  position: absolute;
  right: 10px;
  top: 10px;
  color: #ffffff;
  border-radius: 4px;
  text-align: center;
}

.date-pos span {
  display: block;
  font-size: 30px;
  line-height: 30px;
}