// Breadcrumb.
// @todo: make this a component > at least prefix it with vf-breadcrumb.
.c-breadcrumb {
  float: right;
  display: inline-block;
  margin: 0;
  line-height: 11px;
  padding: 10px 12px 8px;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  word-wrap: break-word;

  @include media-breakpoint-only(xs) {
    float: none;
    margin-right: 0;
    padding-left: 0;
    margin-left: 0;
    padding-right: 0;
  }

  .breadcrumb {
    background-color: transparent;
    margin: 0;
    padding-left: 0;
    padding-right: 0;
  }

  .breadcrumb a {
    color: $white;
  }
}
