// @module
// themes/interface/src/scss/modules/_slick.scss
// Slick
// -----------------------------------------------------------------------------

$slick-arrows-size: 54px;
$slick-arrows-size--xs: 34px;
$slick-btn-bg-color: #fff;
$slick-btn-border-color: $gray-700 !default;
$slick-arrow-color: #000;
$slick-arrow-hover-color: #fff;
$slick-arrow-border-color: #000;

// @todo: should this be a module ?

.slick-slide:focus {
  outline: 0;
}

.slick-dotted.slick-slider {
  margin-bottom: 0;
}

//== Dots
ul.slick-dots {
  position: relative;
  bottom: auto;
  margin: 1rem auto 0;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  margin: 0 8px;
  width: 15px;
  height: 15px;
  cursor: pointer;
}

.slick-dots li button {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: none;
  border-radius: 50%;
  background-color: $slick-btn-bg-color;
  text-indent: -999em;
  cursor: pointer;
  position: absolute;
  border: 2px solid $slick-btn-border-color;
  transition: border-color 0.3s ease;
  padding: 0;
}

.slick-dots li button:before {
  display: none;
}

.slick-dots li button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  background: $primary;
  border-radius: 50%;
  box-shadow: 0 0 1px $primary;
  opacity: 0;
  transform: scale(3);
  transition: opacity 0.3s ease, transform 0.3s ease, visibility 0s 0.3s;
}

.slick-dots li button:hover,
.slick-dots li button:focus,
.slick-dots li.slick-active button {
  border-color: $primary;
}

.slick-dots li.slick-active button::after {
  visibility: visible;
  opacity: 1;
  transform: scale(1.1);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

//== Arrows
.slick-arrow {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  padding: 0;
  transform: translate(0, -50%);
  cursor: pointer;
  color: $slick-arrow-color;
  border: 2px solid $slick-arrow-border-color;
  width: $slick-arrows-size;
  height: $slick-arrows-size;
  outline: none;
  background: transparent;
  transition: background .3s, color .3s, border .3s;

  @include media-breakpoint-only(xs) {
    width: $slick-arrows-size--xs;
    height: $slick-arrows-size--xs;
  }

  &:before {
    display: none;
  }

  &.prev,
  &.slick-prev {
    left: 0;
  }

  &.next,
  &.slick-next {
    right: 0;
  }

  [class^="icon-"],
  [class*=" icon-"] {
    font-size: 18px;
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    padding: 0;
    transform: translate(-50%, -50%);

    @include media-breakpoint-only(xs) {
      font-size: 12px;
    }
  }

  &:hover {
    color: $slick-arrow-hover-color;
    background: $primary;
    border-color: $primary;
  }
}

//== Space for arrows.
.slick-list {
  margin: 0 ($slick-arrows-size + 20px);

  @include media-breakpoint-only(xs) {
    margin: 0 ($slick-arrows-size--xs + 20px);
  }
}

.single-item .slick-list {
  margin: 0;
}

//== Adjust margin for inside-in components.
.slick-slide > * {
  margin-bottom: 0;
}

.slick-use-equal-height .slick-track {
  display: flex;

  .slick-slide {
    height: auto;
  }
}

//== Equal height items.
.eq-height-slider-items .slick-track {
  @include row-make-equal-height();
}

.slick-hide-until-loaded.slick-initialized {
  visibility: visible;
}

.slick-hide-until-loaded:not(.slick-initialized) {
  visibility: hidden;
  display: flex;
}

@include media-breakpoint-down(md) {
  .slick-center-mode {
    .slick-list {
      margin: 0;
    }

    .slick-arrow {
      display: none !important;
    }

    // Padding between elements.
    .slick-track {
      $i: 1;
      $list: ".col-xs-#{$i}, .col-sm-#{$i}, .col-md-#{$i}, .col-lg-#{$i}";
      $slick_items_padding: 10px;

      @for $i from (1 + 1) through $grid-columns {
        $list: "#{$list}, .col-xs-#{$i}, .col-sm-#{$i}, .col-md-#{$i}, .col-lg-#{$i}";
      }

      #{$list} {
        padding-left: $slick_items_padding;
        padding-right: $slick_items_padding;
      }
    }
  }
}
