* {
  outline: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: $body-color;
  font-family: $body-font;
  margin: 0;
  color: $body-text;
  font-weight: $body-text-weight;
}

html {
  position: relative;
  background: $white;
}

a:hover,
a:focus {
  text-decoration: none;
}

a.link {
  color: $headingtext;
  &:hover,
  &:focus {
    color: $themecolor;
  }
}

a.white-link {
  color: $white;
  &:hover,
  &:focus {
    color: $dark;
  }
}

.img-responsive {
  width: 100%;
  height: auto;
  display: inline-block;
}

.img-rounded {
  border-radius: $radius;
}

html body .mdi:before,
html body .mdi-set {
  line-height: initial;
}