// Fonts
// -----------------------------------------------------------------------------
@import url("//fonts.googleapis.com/css?family=Lato:400,700,900,400italic");
@import url('https://fonts.googleapis.com/css?family=PT+Sans');
@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,800');

// Headings
// -----------------------------------------------------------------------------

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $headingtext;
  font-family: $headingfont;
}

h1 {
  line-height: 40px;
  font-size: 36px
}

h2 {
  line-height: 36px;
  font-size: 30px
}

h3 {
  line-height: 30px;
  font-size: 24px
}

h4 {
  line-height: 26px;
  font-size: 21px
}

h5 {
  line-height: 22px;
  font-size: 18px;
  font-weight: 400;
}

h6 {
  line-height: 20px;
  font-size: 16px;
  font-weight: 400;
}

.display-5 {
  font-size: 3rem;
}

.display-6 {
  font-size: 36px;
}

.display-7 {
  font-size: 26px;
}

// Title
// -----------------------------------------------------------------------------
.title {
  margin: 50px 0 60px;
  padding: 0 15px 20px;
  position: relative;
  text-align: center;

  &::after {
    content: "";
    width: 100%;
    max-width: 70px;
    border-bottom: 4px solid $gray-700;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
  }
}

// Subtitle
// -----------------------------------------------------------------------------
.subtitle {
  color: #8d97ad;
  line-height: 24px; }


// Text Colors
// -----------------------------------------------------------------------------
.text-white {
  color: $white !important;
}

.text-danger {
  color: $danger !important;
}

.text-muted {
  color: $muted !important;
}

.text-warning {
  color: $warning !important;
}

.text-success {
  color: $success !important;
}

.text-info {
  color: $info !important;
}

.text-inverse {
  color: $inverse !important;
}

.text-success-gradiant {
  @include linear-gradient (to right, $success 0%, $success-dark 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}

.text-info-gradiant {
  @include linear-gradient (to right, $info 0%, $primary 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}

html body {
  .text-blue {
    color: $blue;
  }
  .text-purple {
    color: $purple;
  }
  .text-primary {
    color: $primary !important;
  }
  .text-megna {
    color: $megna;
  }
  .text-dark {
    color: $body-text;
  }
  .text-themecolor {
    color: $themecolor;
  }
}

// Text Weight & Size.
// -----------------------------------------------------------------------------
html body {
  .font-bold {
    font-weight: 700;
  }
  .font-semibold {
    font-weight: 600;
  }
  .font-normal {
    font-weight: normal;
  }
  .font-light {
    font-weight: 300;
  }
  .font-medium {
    font-weight: 500;
  }
  .font-16 {
    font-size: 16px;
  }
  .font-14 {
    font-size: 14px;
  }
  .font-13 {
    font-size: 13px;
  }
  .font-10 {
    font-size: 10px;
  }
  .font-18 {
    font-size: 18px;
  }
  .font-20 {
    font-size: 20px;
  }
  .font-stylish {
    font-family: $stylishfont;
  }
}

// Background Colors.
// -----------------------------------------------------------------------------
.bg-primary {
  background-color: $primary !important;
}

.bg-success {
  background-color: $success !important;
}

.bg-info {
  background-color: $info !important;
}

.bg-warning {
  background-color: $warning !important;
}

.bg-danger {
  background-color: $danger !important;
}

.bg-orange {
  background-color: $orange !important;
}

.bg-yellow {
  background-color: $yellow;
}

.bg-facebook {
  background-color: $facebook;
}

.bg-twitter {
  background-color: $twitter;
}

.bg-success-gradiant {
  @include linear-gradient (to right, $success 0%, $success-dark 100%);
}

.bg-info-gradiant {
  @include linear-gradient (to right, $info 0%, $primary 100%);
}

.bg-danger-gradiant {
  @include linear-gradient (to right, $danger 0%, $orange 100%);
}