@import "variables";

@font-face {
  font-family: 'vactory';
  src:
    url('#{$icomoon-font-path}/vactory.ttf?uwmyue') format('truetype'),
    url('#{$icomoon-font-path}/vactory.woff?uwmyue') format('woff'),
    url('#{$icomoon-font-path}/vactory.svg?uwmyue#vactory') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'vactory' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

